.App {
  text-align: center;
  background: #222;
  height: 100vh;
}

.Button {
  background: black;
  color: white;
  border: none;
  padding: 12px 32px;
  font-weight: bold;
  opacity: 0.5;
  position: fixed;
  top: 0;
  left: 0;
  margin: 64px;
  outline: none;
}

.Cols {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: space-evenly;
      justify-content: space-evenly;
  -ms-flex-align: baseline;
      align-items: baseline;
}

.bar {
  -webkit-transition: all 100ms;
  -o-transition: all 100ms;
  transition: all 100ms;
  /* margin: 1px; */
}


